.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
  }
  
  .title {
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  .userStatus {
    margin: 20px 0;
  }
  
  .statusContainer {
    display: flex;
    gap: 10px;
    margin-top: 10px;
  }
  
  .statusButton {
    padding: 10px 20px;
    border: 2px solid #ccc;
    border-radius: 5px;
    cursor: pointer;
    background-color: white;
    transition: background-color 0.3s;
  }
  
  .statusButton:hover {
    background-color: #f0f0f0;
  }
  
  .selected {
    background-color: #4caf50; /* Green for selected */
    color: white;
  }
  
  .submitButton,
  .logoutButton {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .submitButton:hover,
  .logoutButton:hover {
    background-color: #0056b3;
  }
  